.navbar{
    width: 100%;
    height: 60px;
     /*position: sticky;*/
    top: 0;
    z-index: 999;
}

.navbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.link{
    display: flex;
    flex-direction: row;
}

.logo{
    min-width: 10%;
    min-height: 10%;
    max-width: 10%;
    max-height: 10%;
    /*border-radius: 50%;*/
    cursor: pointer;
}

.logoName{
    margin-top: 1%;
    margin-left: 2%;
    font-weight: bold;
    font-size: 32px;
    color: black;
    cursor: pointer;
    text-align:justify
}