.mainPage{
    display: flex;
    flex: 4;
    margin-top: 0px;
    margin-left: 0px;
    margin-right: 0px;
    /*min-height: calc(100vh - 60px);*/
    justify-content: space-between;
    position: relative;
    flex-direction: column;
    gap: 30px; 
    flex-wrap: wrap; 
  }

  .row{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    gap: 20px; 
    flex-wrap: wrap; 
  }

  .title_text{
    display: flex;
    flex-direction: column;
    gap: 30px; 
    flex-wrap: wrap; 
    width: 859px;
    height: 472px;
  }
  
  .heading{
    margin-top: 120px;
    width: 800px;
    color: black;
    font-size: 44px;
    font-family: Candal;
    font-weight: 400;
    word-wrap: break-word;
  }
  
  .text-container{
    width: 800px;
    color: black;
    font-size: 26px;
    font-family: Candal;
    font-weight: 400;
    word-wrap: break-word;
    width: 859px;
  }

  .image{
    border: 0.5px solid black;
  }

  .btnDiv{
    display: flex;
    gap: 30px;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 859px;
  }

  .btn{
    width: 200px;
    height: 40px;
    color: greenyellow;
    background-color:green;
    border-radius: 10px;
    font-size: large;
  }